.film-modal-content {
  background-color: #fff;
  padding: 10px 10px 35px;
  transition: transform .5s cubic-bezier(.075, .82, .165, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal {
  z-index: 9999;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  position: absolute;
  top: 0;
  left: 0;
}

.modal__content {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__content img {
  width: 100%;
  max-width: 400px;
  height: auto;
  margin: 0 auto 20px;
  display: block;
}

